<template>
  <div class="container">
    <div class="upload">
      <el-upload class="avatar-uploader" action="https://jsonplaceholder.typicode.com/posts/" :show-file-list="false" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
        <img v-if="imageUrl" :src="imageUrl" class="avatar">
        <Icon type="ios-camera" v-else></Icon>
      </el-upload>
    </div>
    <div class="nickname">
      <input type="text" placeholder="请设置昵称" maxlength="12">
    </div>
    <div class="sex">
      <ul>
        <li @click="man()" :class="sexCheck1">
          男
        </li>
        <li @click="woman()" :class="sexCheck2">
          女
        </li>
      </ul>
    </div>
    <div class="loginBtn">
      <button>
        进入首页
      </button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      imageUrl: "",
      header: {
        token: "Bearer" + localStorage.token
      },
      sexValue: 1,
      sexCheck1: "",
      sexCheck2: ""
    };
  },
  methods: {
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        Toast({
          message: "上传头像图片只能是 JPG 格式!",
          position: "bottom",
          duration: 5000
        });
      }
      if (!isLt2M) {
        Toast({
          message: "上传头像图片大小不能超过 2MB!",
          position: "bottom",
          duration: 5000
        });
      }
      return isJPG && isLt2M;
    },
    man() {
      this.sexValue = 1;
      this.sexCheck1 = "blur";
      this.sexCheck2 = "";
    },
    woman() {
      this.sexValue = 2;
      this.sexCheck1 = "";
      this.sexCheck2 = "blur";
    }
  }
};
</script>

<style lang="less" scoped>
@commonfontsize: 17px;
.container {
  height: 100%;
  background: #666;
  overflow: hidden;
  padding: 0 15px;
  .upload {
    width: 130px;
    height: 130px;
    line-height: 130px;
    text-align: center;
    border-radius: 50%;
    font-size: 70px;
    color: #cbcbcb;
    margin: 0 auto;
    margin-top: 77px;
    position: relative;
    background-color: #fff;
    img {
      width: 100%;
      height: 100%;
    }
    .avatar-uploader {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
    }
    .avatar-uploader .el-upload:hover {
      border-color: #a82372;
      background: linear-gradient(to right, #c8286e, #a82372);
    }
  }
  .nickname {
    margin-top: 20px;
    input {
      height: 44px;
      width: 140px;
      text-align: center;
      border: none;
      background-color: transparent;
      outline: none;
      color: #fff;
      line-height: 44px;
      letter-spacing: 2px;
      display: block;
      margin: 0 auto;
      font-size: @commonfontsize;
      border-bottom: 1px solid #fff;
      &::-webkit-input-placeholder {
        color: #fff;
      }
      &::-moz-placeholder {
        /* Mozilla Firefox 19+ */
        color: #fff;
      }
      &:-moz-placeholder {
        /* Mozilla Firefox 4 to 18 */
        color: #fff;
      }
      &:-ms-input-placeholder {
        /* Internet Explorer 10-11 */
        color: #fff;
      }
    }
  }
  .sex {
    width: 140px;
    height: 44px;
    margin: 0 auto;
    margin-top: 10px;
    ul {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: space-between;
      li {
        width: 60px;
        height: 44px;
        font-size: @commonfontsize;
        line-height: 44px;
        float: left;
        text-align: center;
        border-bottom: 1px solid #fff;
        color: rgba(255, 255, 255, 1);
      }
      .blur {
        color: #544c49;
      }
    }
  }
  .loginBtn {
    button {
      width: 206px;
      height: 45px;
      border-radius: 6px;
      display: block;
      border: none;
      outline: none;
      color: #ffffff;
      font-size: @commonfontsize;
      margin: 0 auto;
      margin-top: 54px;
      letter-spacing: 5px;
      background: linear-gradient(to right, #c8286e, #a82372);
    }
  }
}
</style>





