<template>
  <div class="container">
    <div class="header">
      <mt-header title="查询预约单">
        <router-link to="/" slot="left">
          <mt-button icon="back"></mt-button>
        </router-link>
      </mt-header>
    </div>
    <div class="rose">

    </div>
    <div class="content">
      <div class="bottom">
      </div>
      <div class="letter">
        <swiper :options="swiperOption">
          <swiper-slide v-for="item in booking_list" :key="item.index">
            <div class="letter-text">
              <h3>
                <em>{{item.user_nickname}}</em>你好：</h3>
              <p>
                您的单号
                <em>{{item.order_sn}}</em> 于
                <em>{{item.order_time}}</em> 确定预约
                <em>{{item.hospital_name}}</em> 医院给您提供的
                <em>{{item.project_name}}</em> 项目，当前状态为
                <em>{{item.order_status}}</em> 请保持手机畅通，耐心等待！我们会在24小时内联系您并确认您的请求，请注意查。
              </p>
              <router-link :to="{path:'/getAllowance'+item.order_sn}" class="btn">我已变美！领取补贴！</router-link>
            </div>
            <div class="letter-bottom"></div>
          </swiper-slide>

          <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "booking_search",
  data() {
    return {
      booking_list: [],
      status: ["正在为您安排"],
      swiperOption: {
        slidesPerView: 1,
        spaceBetween: 60,
        centeredSlides: true,
        pagination: {
          el: ".swiper-pagination",
          clickable: true,
          type: "fraction"
        }
      }
    };
  },
  methods: {},
  created() {
    this.axios
      .post("/api/v2_0/beauty_project_order/list", { page: 1, pageSize: 100 })
      .then(response => {
        let res = response.data;
        this.booking_list = res.data.data;
        this.booking_list.forEach(element => {
          if (element.order_time !== "") {
            element.order_time = new Date(
              Math.floor(element.order_time + "000")
            ).toLocaleDateString();
          }
          element.order_status = this.status[element.order_status];
        });
      });
  }
};
</script>

<style lang="less" scoped>
.container {
  width: 100%;
  height: 100%;
  background-image: url("../../assets/images/ce071432be0fa03d47da9294f73e8ee4.png");
  background-size: cover;
  .header {
    background-color: #fff;
    height: 64px;
    .mint-header {
      background-color: #fff;
      color: #444444;
      min-height: 44px;
      line-height: 44px;
      font-size: 17px;
      top: 20px;
    }
  }
  .rose {
    width: 100%;
    height: 204px;
    background-image: url("../../assets/images/rose.png");
    background-size: cover;
    position: absolute;
    top: 64px;
    z-index: 99;
  }
  .content {
    width: 100%;
    height: 422px;
    position: absolute;
    top: 160px;
    left: 50%;
    transform: translateX(-50%);
    .bottom {
      width: 300px;
      height: 100%;
      background: linear-gradient(
        136.2deg,
        rgba(246, 172, 141, 1),
        rgba(238, 135, 107, 1)
      );
      box-shadow: -1px 3px 7px 0px rgba(243, 153, 124, 0.59),
        0px 4px 7px 0px rgba(249, 249, 249, 0.5),
        0px -4px 2px 0px rgba(0, 0, 0, 0.07);
      border-radius: 26px;
      position: absolute;
      top: 0;
      left: 50%;
      transform: rotate(-7deg) translateX(-50%);
    }
    .letter {
      width: 100%;
      height: 120%;
      border-radius: 26px;
      overflow: hidden;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      .swiper-container {
        width: 300px;
        height: 422px;
        position: absolute;
        top: 12px;
        left: 50%;
        transform: translateX(-50%);
        overflow: visible;
        .swiper-slide {
          width: 300px !important;
          height: 422px;
          padding: 0 6px;
          margin-bottom: 20px;
          border-radius: 26px;
          .letter-text {
            width: 96%;
            height: 408px;
            border-radius: 17px;
            border: 2px solid #fff;
            margin-top: 6px;
            background: linear-gradient(
              136.1deg,
              rgba(244, 239, 230, 1),
              rgba(255, 240, 223, 1)
            );
            border-radius: 26px;
            padding: 0 15px;
            padding-top: 46px;
            position: absolute;
            z-index: 999;

            h3 {
              font-size: 14px;
              z-index: 2;
              em {
                color: #55a0fa;
                margin-right: 5px;
              }
            }
            P {
              font-size: 14px;
              letter-spacing: 1px;
              text-indent: 30px;
              line-height: 30px;
              margin-top: 30px;
              z-index: 2;
              text-decoration: underline;
              text-decoration-color: #bbb;
              text-decoration-style: dashed;
              em {
                color: #55a0fa;
              }
            }
            .btn {
              display: block;
              width: 180px;
              height: 45px;
              line-height: 45px;
              text-align: center;
              color: #fff;
              background: linear-gradient(
                134.7deg,
                rgba(222, 43, 107, 1),
                rgba(144, 30, 120, 1)
              );
              border-radius: 2px;
              margin: 0 auto;
              margin-top: 26px;
            }
          }
          .letter-bottom {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background: linear-gradient(
              136.1deg,
              rgba(244, 239, 230, 1),
              rgba(255, 240, 223, 1)
            );
            box-shadow: 0px 4px 7px 0px rgba(0, 0, 0, 0.35);

            z-index: 0;
            border-radius: 26px;
          }
        }
        .swiper-pagination {
          position: fixed;
          bottom: -50px;
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }
  }
}
</style>





