<template>
  <div class="container">
    <div class="top">
      <router-link to="/award_detail" class="award_details">
        奖励流水
      </router-link>
      <div class="header">
        <img src="" alt="">
        <em></em>
        <i>代理商</i>
      </div>
      <div class="all-money">
        <h3>
          您目前的总奖励为：
          <router-link to="getMoney">
            <em></em>
            提现
          </router-link>
        </h3>
        <div class="money">
          1000000.0
        </div>
        <div class="line"></div>
        <ul class="awards">
          <li>
            <h2>6000.0</h2>
            <p>当天奖励</p>
          </li>
          <li>
            <h2>6000.0</h2>
            <p>累计奖励</p>
          </li>
          <li>
            <h2>6000.0</h2>
            <p>累计提取奖励</p>
          </li>
        </ul>
      </div>
    </div>
    <div class="btns">
      <ul>
        <li>
          <router-link to="/my_team" class="btn1">我的团队</router-link>
        </li>
        <li>
          <router-link to="/award_area" class="btn2">地区奖励</router-link>
        </li>
      </ul>
    </div>
    <div class="charts">
      <h2>最近7天奖励走势图</h2>
      <div class="echart" id="main" ref="main">

      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "mkCircle",
  data() {
    return {
      chartData: {
        date: [
          new Date().getDate() - 7 + "日",
          new Date().getDate() - 6 + "日",
          new Date().getDate() - 5 + "日",
          new Date().getDate() - 4 + "日",
          new Date().getDate() - 3 + "日",
          new Date().getDate() - 2 + "日",
          new Date().getDate() - 1 + "日"
        ],
        dataList: []
      }
    };
  },
  methods: {
    //echarts
    showData() {
      var myChart = this.$echarts.init(this.$refs.main);
      var axisData = this.chartData.date;
      var data = this.chartData.dataList;
      var links = data.map(function(item, i) {
        return {
          source: i,
          target: i + 1
        };
      });
      links.pop();
      let option = {
        tooltip: {},
        offset: "-8",
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: axisData,
          name: "日期",
          nameLocation: "start",
          axisLine: {
            fontSize: "10px"
          }
        },
        yAxis: {
          type: "value",
          show: false
        },
        series: [
          {
            type: "graph",
            layout: "none",
            coordinateSystem: "cartesian2d",
            symbolSize: 15,
            color: "#8a5aa2",
            label: {
              normal: {
                show: true
              }
            },
            edgeSymbol: ["circle", "arrow"],
            edgeSymbolSize: [1, 4],
            data: data,
            links: links,
            lineStyle: {
              normal: {
                color: "#c19afd"
              }
            }
          }
        ]
      };
      myChart.setOption(option);
    }
  },
  mounted() {
    this.axios.post("/api/v2_0/user_reward/total").then(response => {
      let res = response.data;
      console.log(res);
      for (const k in res.data.total) {
        this.chartData.dataList.push(res.data.total[k].amount);
      }
      console.log(this.chartData.dataList);
      this.showData();
    });
  }
};
</script>

<style lang="less" scoped>
.container {
  background-color: #f0f0f0;
  .top {
    height: 394px;
    background: url("../../assets/images/2e02ee8648cbd6c63743f6eb0f7c6ad9.png")
      no-repeat;
    background-size: cover;
    padding: 0 15px;
    padding-top: 75px;
    position: relative;
    box-sizing: border-box;
    .award_details {
      width: 56px;
      height: 20px;
      line-height: 20px;
      font-size: 14px;
      display: block;
      color: #fff;
      position: absolute;
      top: 32px;
      right: 15px;
    }
    .header {
      width: 90px;
      height: 90px;
      background-color: rgba(255, 255, 255, 1);
      border-radius: 50%;
      margin: 0 auto;
      position: relative;
      em {
        width: 40px;
        height: 40px;
        display: block;
        background: url("../../assets/images/huangguan.png") 0 0;
        background-size: contain;
        position: absolute;
        top: -21px;
        right: 63%;
      }
      i {
        width: 58px;
        height: 24px;
        line-height: 24px;
        text-align: center;
        font-size: 10px;
        color: #fff;
        display: block;
        background: linear-gradient(
          134.7deg,
          rgba(222, 43, 107, 1),
          rgba(144, 30, 120, 1)
        );
        border-radius: 12px;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        font-style: normal;
      }
    }
    .all-money {
      color: #fff;
      h3 {
        font-size: 17px;
        line-height: 17px;
        margin-top: 25px;
        overflow: hidden;
        vertical-align: middle;
        a {
          float: right;
          color: #fff;
          font-size: 17px;
          line-height: 17px;
          em {
            display: inline-block;
            width: 16px;
            height: 19px;
            background: url("../../assets/images/手指.png");
            background-size: cover;
          }
        }
      }
      .money {
        width: 100%;
        font-size: 50px;
        color: #fff;
        text-align: center;
        margin: 10px 0;
        line-height: 70px;
      }
      .line {
        width: 100%;
        height: 1px;
        background-color: #fff;
        margin: 0 auto;
      }
      .awards {
        width: 100%;
        display: flex;
        margin-top: 15px;
        li {
          flex: 1;
          text-align: center;
          h2 {
            font-size: 20px;
          }
          p {
            font-size: 14px;
            color: rgba(255, 255, 255, 0.62);
            margin-top: 6px;
          }
        }
      }
    }
  }

  .btns {
    width: 100%;
    height: 80px;
    background-color: #fff;
    margin-bottom: 10px;
    ul {
      width: 100%;
      height: 100%;
      li {
        height: 100%;
        width: 50%;
        float: left;
        a {
          display: block;
          width: 90%;
          height: 50px;
          color: #fff;
          line-height: 50px;
          margin: 15px auto;
          text-align: center;
        }
        .btn1 {
          background: linear-gradient(
            135deg,
            rgba(239, 156, 255, 1),
            rgba(184, 97, 255, 1)
          );
          box-shadow: 0px 2px 4px 0px rgba(213, 128, 255, 1);
          border-radius: 2px;
          margin-right: 7px;
        }
        .btn2 {
          background: linear-gradient(
            135deg,
            rgba(193, 154, 253, 1),
            rgba(148, 121, 248, 1)
          );
          box-shadow: 0px 2px 4px 0px rgba(180, 144, 252, 1);
          border-radius: 2px;
          margin-left: 7px;
        }
      }
    }
  }

  .charts {
    width: 100%;
    background-color: #fff;
    position: relative;
    box-sizing: border-box;
    h2 {
      width: 100%;
      height: 37px;
      line-height: 37px;
      text-align: center;
      font-size: 12px;
      color: #626262;
      position: absolute;
      top: 15px;
    }
    .echart {
      width: 100%;
      height: 300px;
    }
  }
}
</style>




