<template>
  <div class="container">
    <div class="title">
      <div class="logo">
        <img src="../../assets/images/icon_register_logo@3x.png" alt="">
      </div>
      <div class="text">
        请绑定您的手机号
      </div>
  </div>
  <div class="loginForm">
    <div class="tel">
      <input type="tel" placeholder="请输入手机号" class="userTel">
      <span><img src="../../assets/images/icon_first_phone@3x.png" alt=""></span>
    </div>
    <div class="pwd">
      <input type="password" placeholder="请输入登录密码" class="userPwd">
      <span><img src="../../assets/images/icon_first_password@3x.png" alt=""></span>
    </div>
    <div class="checkNumber">
      <input type="number" class="checkNumInp" placeholder="请输入验证码"> 
      <div class="getCheckNum">
          点击获取验证码
      </div>
    </div>
    <div class="protocol">
        <input type="checkbox" id="isAgree"><label for="isAgree"> 您确认加入则同意 <em>《美美咖用户协议》</em></label>
    </div>
    <div class="loginBtn">
      <button> 
        提交
      </button>
    </div>
  </div>
  <div class="registerBtn">
    <button> 
       取消
    </button>
  </div>

 </div>
</template>

<script>
export default {
  name: "bindingTel"
};
</script>

<style lang="less" scoped>
@commonFontSize: 15px;
.container {
  height: 100%;
  background: #666;
  overflow: hidden;
  padding: 0 15px;
  //头部标题
  .title {
    height: 40px;
    margin-top: 80px;
    .logo {
      height: 100%;
      float: left;
      margin-right: 10px;
      img {
        height: 100%;
      }
    }
    .text {
      line-height: 40px;
      color: #fff;
      font-size: 2rem;
    }
  }
  //注册表单
  .loginForm {
    padding-top: 35px;
    overflow: hidden;
    .userTel,
    .userPwd {
      height: 44px;
      width: 100%;
      margin-bottom: 15px;
      border: none;
      background-color: transparent;
      outline: none;
      padding-left: 28px;
      color: #fff;
      line-height: 44px;
      letter-spacing: 2px;
      font-size: @commonFontSize;
      border-bottom: 1px solid #fff;
      &::-webkit-input-placeholder {
        color: #fff;
      }
    }
    .tel,
    .pwd {
      position: relative;
      span {
        width: 13px;
        height: 18px;
        display: block;
        position: absolute;
        top: 10px;
        left: 0;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
    .checkNumber {
      overflow: hidden;
      display: flex;
      .checkNumInp {
        height: 44px;
        border: none;
        float: left;
        background-color: transparent;
        outline: none;
        color: #fff;
        line-height: 44px;
        letter-spacing: 2px;
        font-size: @commonFontSize;
        flex: 1;
        border-bottom: 1px solid #fff;
        &::-webkit-input-placeholder {
          color: #fff;
        }
      }
      .getCheckNum {
        float: right;
        flex: 1;
        font-size: @commonFontSize;
        text-align: center;
        line-height: 44px;
        font-weight: bolder;
        letter-spacing: 2px;
        color: #fff;
      }
    }
    .protocol {
      width: 100%;
      text-align: center;
      color: #fff;
      font-size: 10px;
      margin-top: 20px;
      em {
        text-decoration: underline;
      }
      input {
        width: 10px;
        height: 10px;
        border: 0;
        outline: none;
      }
    }
    .loginBtn {
      button {
        width: 206px;
        height: 45px;
        border-radius: 6px;
        display: block;
        border: none;
        outline: none;
        color: #ffffff;
        font-size: @commonFontSize;
        margin: 0 auto;
        margin-top: 55px;
        letter-spacing: 5px;
        background: linear-gradient(to right, #c8286e, #a82372);
      }
    }
  }
  .registerBtn {
    button {
      width: 206px;
      height: 45px;
      border-radius: 6px;
      display: block;
      border: none;
      outline: none;
      color: #ffffff;
      font-size: @commonFontSize;
      margin: 0 auto;
      margin-top: 15px;
      letter-spacing: 5px;
      background: RGBA(130, 127, 127, 0.8);
    }
  }
}
</style>




