<template>
  <div class="container">
    <!-- 头部 -->
    <mt-header title="医院介绍" fixed>
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      <mt-button icon="search" slot="right"></mt-button>
    </mt-header>
    <!-- 医院列表 -->
    <div class="hospital-list">
      <Affix :offset-top="44">
        <div class="top">
          <ul class="list-controller">
            <li class="controller-item">
              <Dropdown trigger="click" @on-click="selectarea">
                <a href="javascript:void(0)">
                  地区
                  <Icon type="arrow-down-b"></Icon>
                </a>
                <DropdownMenu slot="list" v-for="item in areaList" :key="item.index">
                  <Dropdown placement="right-start">
                    <DropdownItem name=item.id>
                      {{item.name}}
                      <Icon type="ios-arrow-right"></Icon>
                    </DropdownItem>
                    <DropdownMenu slot="list">
                      <DropdownItem v-for="value in item.children" :key="value.index" name=value.id>{{value.name}}</DropdownItem>
                    </DropdownMenu>
                  </Dropdown>

                </DropdownMenu>
              </Dropdown>
            </li>
            <li class="controller-item">
              <Dropdown trigger="click" @on-click="changelevel">
                <a href="javascript:void(0)">
                  美咖星级
                  <Icon type="arrow-down-b"></Icon>
                </a>
                <DropdownMenu slot="list">
                  <DropdownItem name="0">一级</DropdownItem>
                  <DropdownItem name="1">二级</DropdownItem>
                  <DropdownItem name="2">三级</DropdownItem>
                </DropdownMenu>
              </Dropdown>
            </li>
            <li class="controller-item">
              <Dropdown trigger="click" @on-click="nearly">
                <a href="javascript:void(0)">
                  离我最近
                  <Icon type="arrow-down-b"></Icon>
                </a>
                <DropdownMenu slot="list">
                  <DropdownItem name="1">1km</DropdownItem>
                  <DropdownItem name="2">2km</DropdownItem>
                  <DropdownItem name="3">3km</DropdownItem>
                  <DropdownItem name="4">4km</DropdownItem>
                  <DropdownItem name="5">5km</DropdownItem>
                </DropdownMenu>
              </Dropdown>
            </li>
            <li class="controller-item">
              <Dropdown placement="bottom-end" trigger="click" @on-click="score">
                <a href="javascript:void(0)">
                  评分
                  <Icon type="arrow-down-b"></Icon>
                </a>
                <DropdownMenu slot="list">
                  <DropdownItem name=5>5.0</DropdownItem>
                  <DropdownItem name=4>4.0</DropdownItem>
                  <DropdownItem name=3>3.0</DropdownItem>
                  <DropdownItem name=2>2.0</DropdownItem>
                  <DropdownItem name=1>1.0</DropdownItem>
                  <DropdownItem name=0>0.0</DropdownItem>
                </DropdownMenu>
              </Dropdown>
            </li>
          </ul>
        </div>
      </Affix>
      <ul class="content">
        <li class="list-item" v-for="item in  hospitalList" :key="item.id">
          <router-link :to="{path:'/hospital_detail:'+item.id}">
            <div class="left">
              <img :src=item.image alt="">
            </div>
            <div class="right">
              <h2>
                {{item.name}}
                <span class="hospital-level">
                  <em>V</em>
                  <i v-text="level_list[item.level-1].msg"></i>
                </span>
              </h2>
              <div class="hospital-star">
                <Rate disabled v-model="item.star"></Rate>
              </div>
              <div class="address">
                <Icon type="ios-location-outline"></Icon>
                <p>{{item.address}}</p>
              </div>
              <ul class="feature">
                <li class="feature-item" v-for="value in item.top_project" :key="value.id">
                  <b>顶</b>
                  <span>{{value.name}}</span>
                </li>
              </ul>
            </div>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "hospital_list",
  data() {
    return {
      value: 5,
      hospitalList: "",
      level_list: [
        {
          msg: "一级",
          color:
            "linear-gradient(138.4deg,rgba(222, 43, 107, 1),rgba(144, 30, 120, 1));"
        },
        {
          msg: "二级",
          color:
            "linear-gradient(138.4deg,rgba(222, 43, 107, 1),rgba(144, 30, 120, 1));"
        },
        {
          msg: "三级",
          color:
            "linear-gradient(138.4deg,rgba(222, 43, 107, 1),rgba(144, 30, 120, 1));"
        },
        {
          msg: "四级",
          color:
            "linear-gradient(138.4deg,rgba(222, 43, 107, 1),rgba(144, 30, 120, 1));"
        },
        {
          msg: "五级",
          color:
            "linear-gradient(138.4deg,rgba(222, 43, 107, 1),rgba(144, 30, 120, 1));"
        }
      ],
      areaList: [],
      lists: ""
    };
  },
  methods: {
    // 筛选城市
    selectarea: function(name) {
      console.log(name);
      let arr = [];
      this.lists.forEach(element => {
        if (element.city == name) {
          arr.push(element);
        }
      });
      this.hospitalList = arr;
    },
    // 筛选美咖星级
    changelevel: function(name) {
      console.log(name);
      let arr = [];
      this.lists.forEach(element => {
        if (element.level == name) {
          arr.push(element);
        }
      });
      this.hospitalList = arr;
    },
    // 筛选离我最近
    nearly: function(name) {
      console.log(name);
    },
    // 筛选评分
    score: function(name) {
      console.log(name);
      let arr = [];
      this.lists.forEach(element => {
        if (element.star == name) {
          arr.push(element);
        }
      });
      this.hospitalList = arr;
    }
  },
  created() {
    // 获取医院列表
    this.axios
      .post("api/v2_0/hospital/list", { page: 1, pageSize: 100 })
      .then(response => {
        let res = response.data;
        this.hospitalList = res.data.data;
        this.hospitalList.forEach(element => {
          element.image = this.imgUrl + element.image;
        });
        this.lists = this.hospitalList;
        console.log(this.hospitalList);
      });
    // 获取城市列表
    this.axios
      .post("/api/v2_0/area/open_prov_city", { is_open: 1 })
      .then(response => {
        let res = response.data;
        if (res.status_code == "0") {
          this.areaList = res.data.data[0].children;
        }
        // console.log(this.hospitalList);
      });
  }
};
</script>

<style lang="less" scoped>
.container {
  // 头部
  padding-top: 44px;
  .mint-header {
    height: 44px;
    background-color: #fff;
    color: #444444;
    font-size: 17px;
    padding: 0 15px;
    border-bottom: 1px solid #eee;
    z-index: 999;
  }
  // 医院列表
  .hospital-list {
    .top {
      width: 100%;
      height: 50px;
      box-shadow: 0px 1px 0px 0px rgba(0, 0, 0, 0.1);
      z-index: 999;
      background-color: #fff;
      .list-controller {
        width: 100%;
        height: 100%;
        display: flex;
        .controller-item {
          flex: 1;
          text-align: center;
          div {
            height: 100%;
            a {
              width: 100%;
              text-align: center;
              line-height: 50px;
              color: #444;
              font-size: 14px;
            }
          }
        }
      }
      .ivu-dropdown-menu {
        max-height: 500px !important;
        overflow: auto !important;
      }
    }
    .content {
      padding: 0 15px;
      .list-item {
        padding: 15px 0;
        overflow: hidden;
        display: flex;
        border-bottom: 1px solid #d8d8d8;
        a {
          width: 100%;
          height: 100%;
          .left {
            width: 100px;
            height: 150px;
            background-color: #666;
            float: left;
            img {
              width: 100%;
              height: 100%;
            }
          }
          .right {
            float: left;
            margin-left: 15px;
            width: 65%;
            h2 {
              width: 100%;
              font-size: 17px;
              color: #222;
              overflow: hidden;
              .hospital-level {
                display: block;
                float: right;
                margin-right: 5px;
                position: relative;
                em {
                  font-style: normal;
                  font-size: 16px;
                  width: 20px;
                  height: 20px;
                  line-height: 20px;
                  text-align: center;
                  border-radius: 50%;
                  background-color: #b02473;
                  background: linear-gradient(
                    138.4deg,
                    rgba(222, 43, 107, 1),
                    rgba(144, 30, 120, 1)
                  );
                  color: #fff;
                  float: left;
                  z-index: 10;
                  position: absolute;
                  left: 0;
                }
                i {
                  font-style: normal;
                  font-size: 8px;
                  display: block;
                  float: left;
                  width: 44px;
                  height: 14px;
                  line-height: 14px;
                  text-align: center;
                  background: linear-gradient(
                    138.4deg,
                    rgba(222, 43, 107, 1),
                    rgba(144, 30, 120, 1)
                  );
                  color: #fff;
                  margin-left: 12px;
                  margin-top: 2.5px;
                  transform: skew(-14deg);
                  font-weight: normal;
                }
              }
            }
            .hospital-star {
              .ivu-rate {
                font-size: 14px;
              }
              .ivu-rate-star-full {
                margin-right: 1px;
              }
            }
            .address {
              overflow: hidden;
              i {
                height: 100%;
                float: left;
                font-size: 16px;
                color: #7dafff;
                margin-right: 9px;
              }
              p {
                width: 90%;
                float: left;
                font-size: 12px;
                color: #888;
              }
            }
            .feature {
              margin-top: 20px;
              .feature-item {
                b {
                  display: inline-block;
                  font-weight: normal;
                  width: 18px;
                  height: 18px;
                  line-height: 18px;
                  text-align: center;
                  color: #fff;
                  font-size: 8px;
                  border-radius: 50%;
                  background: linear-gradient(
                    180deg,
                    rgba(255, 160, 160, 1),
                    rgba(255, 70, 70, 1)
                  );
                  box-shadow: 0px 1px 2px 0px rgba(255, 102, 56, 0.79);
                }
                span {
                  color: #666;
                  font-size: 12px;
                }
                &:nth-of-type(1) {
                  margin-bottom: 10px;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>




