<template>
  <div class="container">
    <!-- 头部 -->
    <div class="header">
      <mt-header title="报单查询">
        <router-link to="/" slot="left">
          <mt-button icon="back"></mt-button>
        </router-link>
      </mt-header>
    </div>
    <!-- 医院图片 -->
    <div class="hospital-image">
      <img src="../../assets/images/凯恩国际背景图.png" alt="" class="mainimg">
      <div class="evaluate">
        <div class="hospital-logo">
          <img src="../../assets/images/凯恩国际LOGO.png" alt="">
        </div>
        <div class="levels">
          <div class="hospital-name">{{hospitalData.name}}</div>

          <span class="hospital-level">
            <em>V</em>
            <i>{{level_list[hospitalData.level].name}}</i>
          </span>
        </div>
        <div class="score">
          <ul class="score-list">
            <li>效果：
              <em>{{hospitalData.effect_score}}</em>
            </li>
            <li>服务：
              <em>{{hospitalData.service_score}}</em>
            </li>
            <li>医生：
              <em>{{hospitalData.doctor_score}}</em>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <!-- 通知 -->
    <div class="notice">
      <Icon type="volume-low"></Icon>
      <span>公告</span>
      <div class="info">
        <ul class="notice-list">
          <!-- swiper -->
          <swiper :options="swiperOption_notice">
            <swiper-slide v-for="item in noticeList" :key="item.id">{{item.content}}</swiper-slide>
          </swiper>

        </ul>
        <div class="right">
          <Icon type="chevron-right"></Icon>
        </div>
      </div>

    </div>
    <!-- 医院介绍 -->
    <div class="hospital-info">
      <h3 class="title">
        医院介绍
        <div class="right">
          <Icon type="chevron-right"></Icon>
        </div>
      </h3>
      <p v-html="hospitalData.descript">
        <!-- 武汉楷恩国际健康管理有限公司源于美国，是一家集生命科学研究、健康管理·抗衰老、医学美容、海外医疗、营养保健品开发于一体的综合型国际医疗企业，中国总部位于湖北省武汉市。 -->
      </p>
    </div>
    <!-- 医生介绍 -->
    <div class="doctors-info">
      <h3 class="title">
        医生介绍
        <div class="right">
          <Icon type="chevron-right"></Icon>
        </div>
      </h3>
      <div class="content">
        <!-- swiper -->
        <swiper :options="swiperOption">
          <swiper-slide v-for="item in doctorsData" :key="item.id">
            <router-link to="#">
              <div class="image">
                <img :src=item.image alt="">
              </div>
              <h4>{{item.name}}</h4>
              <p>{{item.post}}</p>
            </router-link>
          </swiper-slide>

          <!-- <div class="swiper-pagination" slot="pagination"></div> -->
        </swiper>
      </div>
    </div>
    <!-- 商品列表 -->
    <div class="production">
      <Affix :offset-top="44">
        <div class="top">
          <ul class="list-controller">
            <li class="controller-item">
              <Dropdown>
                <a href="javascript:void(0)">
                  部位
                  <Icon type="arrow-down-b"></Icon>
                </a>
                <DropdownMenu slot="list">
                  <DropdownItem>驴打滚</DropdownItem>
                  <DropdownItem>炸酱面</DropdownItem>
                  <DropdownItem>豆汁儿</DropdownItem>
                  <Dropdown placement="right-start">
                    <DropdownItem>
                      北京烤鸭
                      <Icon type="ios-arrow-right"></Icon>
                    </DropdownItem>
                    <DropdownMenu slot="list">
                      <DropdownItem>挂炉烤鸭</DropdownItem>
                      <DropdownItem>焖炉烤鸭</DropdownItem>
                    </DropdownMenu>
                  </Dropdown>
                  <DropdownItem>冰糖葫芦</DropdownItem>
                </DropdownMenu>
              </Dropdown>
            </li>
            <li class="controller-item">
              <Dropdown>
                <a href="javascript:void(0)">
                  价格
                  <Icon type="arrow-down-b"></Icon>
                </a>
                <DropdownMenu slot="list">
                  <DropdownItem>驴打滚</DropdownItem>
                  <DropdownItem>炸酱面</DropdownItem>
                  <DropdownItem>豆汁儿</DropdownItem>
                  <DropdownItem>冰糖葫芦</DropdownItem>
                  <DropdownItem>北京烤鸭</DropdownItem>
                </DropdownMenu>
              </Dropdown>
            </li>
            <li class="controller-item">
              <Dropdown>
                <a href="javascript:void(0)">
                  销量
                  <Icon type="arrow-down-b"></Icon>
                </a>
                <DropdownMenu slot="list">
                  <DropdownItem>驴打滚</DropdownItem>
                  <DropdownItem>炸酱面</DropdownItem>
                  <DropdownItem>豆汁儿</DropdownItem>
                  <DropdownItem>冰糖葫芦</DropdownItem>
                  <DropdownItem>北京烤鸭</DropdownItem>
                </DropdownMenu>
              </Dropdown>
            </li>
            <li class="controller-item">
              <Dropdown placement="bottom-end">
                <a href="javascript:void(0)">
                  最新上架
                  <Icon type="arrow-down-b"></Icon>
                </a>
                <DropdownMenu slot="list">
                  <DropdownItem>驴打滚</DropdownItem>
                  <DropdownItem>炸酱面</DropdownItem>
                  <DropdownItem>豆汁儿</DropdownItem>
                  <DropdownItem>冰糖葫芦</DropdownItem>
                  <DropdownItem>北京烤鸭</DropdownItem>
                </DropdownMenu>
              </Dropdown>
            </li>

          </ul>
        </div>
      </Affix>
      <ul class="content">
        <li class="list-item" v-for="item in projectList" :key="item.id">
          <router-link to="#">
            <div class="image">
              <img :src=item.image alt="">
            </div>
            <h3>{{item.name}}</h3>
            <p>{{item.descript}}</p>
            <div class="price">
              预约价
              <em>￥</em>
              <strong class="num">{{item.appointment_price}}</strong>
            </div>
          </router-link>
        </li>

        <div class="line"></div>
        <div class="list-footer">
          <span>&nbsp;到底啦 ! 商品正在努力上架中 !&nbsp;</span>
        </div>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "hospital_detail",
  data() {
    return {
      imgUrl: this.imgUrl,
      hospitalData: "",
      doctorsData: "",
      projectList: "",
      noticeList: "",
      level_list: [
        {
          name: "一级",
          color:
            "linear-gradient(138.4deg,rgba(222, 43, 107, 1),rgba(144, 30, 120, 1));"
        },
        {
          name: "二级",
          color:
            "linear-gradient(138.4deg,rgba(222, 43, 107, 1),rgba(144, 30, 120, 1));"
        },
        {
          name: "三级",
          color:
            "linear-gradient(138.4deg,rgba(222, 43, 107, 1),rgba(144, 30, 120, 1));"
        },
        {
          name: "四级",
          color:
            "linear-gradient(138.4deg,rgba(222, 43, 107, 1),rgba(144, 30, 120, 1));"
        },
        {
          name: "五级",
          color:
            "linear-gradient(138.4deg,rgba(222, 43, 107, 1),rgba(144, 30, 120, 1));"
        }
      ],
      swiperOption: {
        slidesPerView: 4,
        spaceBetween: 15,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      swiperOption_notice: {
        autoplay: true,
        speed: 1000,
        direction: "vertical",
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      }
    };
  },
  created() {
    let hospital_id = location.hash.split(":")[1];
    // 获取医院详情
    this.axios
      .post("/api/v2_0/hospital/detail", { id: hospital_id })
      .then(response => {
        let res = response.data;
        this.hospitalData = res.data;
      });
    // 获取医生列表
    this.axios
      .post("/api/v2_0/doctor/list", {
        page: 0,
        pageSize: 100,
        id: hospital_id
      })
      .then(response => {
        let res = response.data;
        this.doctorsData = res.data.data;
        this.doctorsData.forEach(element => {
          element.image = this.imgUrl + element.image;
        });
      });
    // 获取商品列表
    this.axios
      .post("/api/v2_0/beauty_project/pushlist", {
        page: 0,
        pageSize: 100,
        hospital_id: hospital_id
      })
      .then(response => {
        let res = response.data;
        this.projectList = res.data.data;
        this.projectList.forEach(element => {
          element.image = this.imgUrl + element.image;
        });
      });
    // 获取公告列表
    this.axios
      .post("/api/v2_0/hospital/notice", { id: hospital_id })
      .then(response => {
        let res = response.data;
        this.noticeList = res.data;
        console.log(this.noticeList);
      });
  }
};
</script>

<style lang="less" scoped>
.container {
  background-color: #f0f0f0;
  //头部
  .header {
    background-color: #fff;
    height: 64px;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 999;
    box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.07);
    .mint-header {
      background-color: #fff;
      color: #444444;
      min-height: 44px;
      line-height: 44px;
      font-size: 17px;
      top: 20px;
    }
  }
  // 医院图片
  .hospital-image {
    margin-top: 64px;
    width: 100%;
    height: 160px;
    background-color: #666;
    position: relative;
    .mainimg {
      width: 100%;
      height: 100%;
    }
    .evaluate {
      height: 60px;
      width: 90%;
      position: absolute;
      bottom: 16px;
      left: 16px;
      z-index: 1;
      .hospital-logo {
        height: 60px;
        width: 60px;
        float: left;
        img {
          width: 100%;
          height: 100%;
          display: block;
        }
      }
      .levels {
        float: left;
        width: 120px;
        margin-left: 11px;
        overflow: hidden;
        margin-right: 100px;
        padding-top: 10px;
        .hospital-name {
          font-style: 17px;
          color: #fff;
          float: left;
        }
        .hospital-level {
          display: block;
          float: right;
          margin-right: 5px;
          position: relative;
          em {
            font-style: normal;
            font-size: 12px;
            width: 18px;
            height: 18px;
            line-height: 18px;
            text-align: center;
            border-radius: 50%;
            background-color: #b02473;
            color: #fff;
            float: left;
            z-index: 10;
            position: absolute;
            left: 0;
          }
          i {
            font-style: normal;
            font-size: 8px;
            display: block;
            float: left;
            width: 40px;
            height: 14px;
            line-height: 14px;
            text-align: center;
            background: linear-gradient(
              138.4deg,
              rgba(222, 43, 107, 1),
              rgba(144, 30, 120, 1)
            );
            color: #fff;
            margin-left: 12px;
            margin-top: 2.5px;
            transform: skew(-14deg);
            font-weight: normal;
          }
        }
      }
      .score {
        float: left;
        margin-left: 11px;
        margin-top: 8px;

        .score-list {
          overflow: hidden;
          li {
            float: left;
            margin-right: 10px;
            color: #fff;
            em {
              color: #ff4343;
            }
          }
        }
      }
    }
  }
  // 通知
  .notice {
    height: 36px;
    line-height: 36px;
    background-color: #fff;
    margin-top: 10px;
    color: #666;
    padding: 0 15px;
    i {
      float: left;
      font-size: 24px;
      line-height: 36px;
      display: block;
      height: 100%;
      float: left;
    }
    span {
      font-size: 12px;
      line-height: 36px;
      display: block;
      height: 100%;
      float: left;
      margin-left: 6px;
    }
    .info {
      height: 100%;
      width: 80%;
      float: right;
      .notice-list {
        width: 90%;
        height: 100%;
        float: left;
        overflow: hidden;
        .swiper-slide {
          width: 100%;
        }
      }
      .right {
        float: right;
        i {
          font-size: 12px;
        }
      }
    }
  }
  // 医院介绍
  .hospital-info {
    width: 100%;
    padding: 0 15px;
    margin-top: 10px;
    background-color: #fff;
    overflow: hidden;
    .right {
      float: right;
      font-size: 12px;
    }
    .title {
      margin: 13px 0;
      padding-left: 10px;
      position: relative;
      font-size: 14px;
      color: #666;
      &::before {
        content: "";
        display: block;
        width: 3px;
        height: 12px;
        background-color: #ff6363;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    p {
      font-size: 14px;
      color: #444;
      padding-bottom: 15px;
    }
  }
  // 医生介绍
  .doctors-info {
    width: 100%;
    padding: 0 15px;
    padding-bottom: 15px;
    margin-top: 10px;
    background-color: #fff;
    overflow: hidden;
    .right {
      float: right;
      font-size: 12px;
    }
    .title {
      margin: 13px 0;
      padding-left: 10px;
      position: relative;
      font-size: 14px;
      color: #666;
      &::before {
        content: "";
        display: block;
        width: 3px;
        height: 12px;
        background-color: #6299ff;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    a {
      display: block;
      width: 100%;
      height: 100%;
      .image {
        width: 100%;
        height: 100px;
        margin: 0 auto;
        background-color: #666;
        img {
          width: 100%;
          height: 100%;
        }
      }
      h4 {
        font-size: 14px;
        color: #444;
        width: 100%;
        text-align: center;
        margin-top: 8px;
      }
      p {
        font-size: 10px;
        color: #666;
        width: 100%;
        text-align: center;
        margin-top: 4px;
      }
    }
  }
  // 商品列表
  .production {
    width: 100%;
    background-color: #fff;
    box-sizing: border-box;
    .top {
      width: 100%;
      height: 50px;
      box-shadow: 0px 1px 0px 0px rgba(0, 0, 0, 0.1);
      z-index: 999;
      background-color: #fff;
      .list-controller {
        width: 100%;
        height: 100%;
        display: flex;
        .ivu-dropdown {
          position: relative;
        }
        .controller-item {
          flex: 1;
          text-align: center;
          div {
            height: 100%;
            a {
              width: 100%;
              text-align: center;
              line-height: 50px;
              color: #444;
              font-size: 14px;
            }
          }
        }
      }
    }
    .content {
      padding: 0 8px;
      overflow: hidden;
      padding-bottom: 120px;
      margin-bottom: 60px;
      margin-top: 15px;
      position: relative;
      .list-item {
        width: 50%;
        float: left;
        padding: 0 8px;
        margin-bottom: 15px;
        .image {
          width: 100%;
          height: 180px;
          background-color: #ccc;
          margin: 0 auto;
          img {
            width: 100%;
            height: 100%;
          }
        }
        h3 {
          font-size: 14px;
          color: #222;
          margin-top: 10px;
        }
        p {
          font-size: 10px;
          color: #888;
          margin-top: 6px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
          text-overflow: ellipsis;
          height: 40px;
          width: 100%;
        }
        .price {
          font-size: 10px;
          color: #888;
          margin-top: 16px;
          em {
            font-size: 12px;
          }
          .num {
            color: #ff7474;
            font-size: 17px;
          }
        }
      }
      .line {
        width: 90%;
        height: 1px;
        background-color: #d8d8d8;
        position: absolute;
        bottom: 64px;
        left: 50%;
        transform: translateX(-50%);
      }
      .list-footer {
        width: 100%;
        height: 20px;
        position: absolute;
        bottom: 32px;
        left: 50%;
        transform: translatex(-50%);
        &:before {
          content: "";
          display: block;
          width: 70%;
          height: 1px;
          background-color: #d8d8d8;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translateX(-50%);
        }
        span {
          width: 170px;
          height: 20px;
          text-align: center;
          background-color: #fff;
          line-height: 20px;
          display: block;
          position: absolute;
          color: #d8d8d8;
          top: 0;
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }
  }
}
</style>





