<template>
  <div class="container">
    <div class="title">
      <div class="logo">
        <img src="../../assets/images/icon_register_logo@3x.png" alt="">
      </div>
      <div class="text">
        注册
      </div>
    </div>
    <div class="loginForm">
      <div class="tel">
        <input type="tel" placeholder="请输入手机号" class="userTel" id="name" v-model="name">
        <span><img src="../../assets/images/icon_first_phone@3x.png" alt=""></span>
      </div>
      <div class="pwd">
        <input type="password" placeholder="请输入登录密码" class="userPwd" id='userPwd' v-model="userPwd">
        <span><img src="../../assets/images/icon_first_password@3x.png" alt=""></span>
      </div>
      <div class="checkNumber">
        <input type="number" class="checkNumInp" placeholder="请输入验证码" v-model="checkcode" id='checkcode'>
        <div class="getCheckNum">
          点击获取验证码
        </div>
      </div>
      <div class="protocol">
        <input type="checkbox" id="isAgree">
        <label for="isAgree"> 您确认加入则同意
          <em>《美美咖用户协议》</em>
        </label>
      </div>
      <div class="loginBtn">
        <button v-on:click='resgiterBtn'>
          提交
        </button>
      </div>
    </div>
    <div class="registerBtn">
      <button>
        取消
      </button>
    </div>

  </div>
</template>

<script>
export default {
  data() {
    return {
      name: "",
      userPwd: "",
      checkcode: ""
    };
  },
  methods: {
    resgiterBtn: function(event) {
      var name = this.name,
        password = this.userPwd,
        checkcode = this.checkcode,
        tel = /^[1][3,4,5,7,8][0-9]{9}$/,
        nickname = /^([a-zA-Z0-9_\u4e00-\u9fa5]){1,16}$/;
      if (name == "" || password == "") {
        this.$message({
          message: "账号或密码为空",
          type: "error"
        });
        return;
      }
      if (!tel.test(name)) {
        this.$message({
          message: "请输入正确的手机号码",
          type: "error"
        });
        return;
      }
      if (!nickname.test(password)) {
        this.$message({
          message: "请重新输入",
          type: "error"
        });
        return;
      }
      $.ajax({
        url: "http://test.api2.ameimeika.com/api/auth/register", //注册
        type: "POST",
        data: {
          mobile: name,
          password: password,
          verify_code: checkcode
        },
        success: function(data) {
          console.log(data);
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
@commonFontSize: 15px;
.container {
  height: 100%;
  background: #666;
  overflow: hidden;
  padding: 0 15px;
  //头部标题
  .title {
    height: 40px;
    margin-top: 80px;
    .logo {
      height: 100%;
      float: left;
      margin-right: 10px;
      img {
        height: 100%;
      }
    }
    .text {
      line-height: 40px;
      color: #fff;
      font-size: 2rem;
    }
  }
  //注册表单
  .loginForm {
    padding-top: 35px;
    overflow: hidden;
    .userTel,
    .userPwd {
      height: 44px;
      width: 100%;
      margin-bottom: 15px;
      border: none;
      background-color: transparent;
      outline: none;
      padding-left: 28px;
      color: #fff;
      line-height: 44px;
      letter-spacing: 2px;
      font-size: @commonFontSize;
      border-bottom: 1px solid #fff;
      &::-webkit-input-placeholder {
        color: #fff;
      }
    }
    .tel,
    .pwd {
      position: relative;
      span {
        width: 13px;
        height: 18px;
        display: block;
        position: absolute;
        top: 10px;
        left: 0;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
    .checkNumber {
      overflow: hidden;
      display: flex;
      .checkNumInp {
        height: 44px;
        border: none;
        float: left;
        background-color: transparent;
        outline: none;
        color: #fff;
        line-height: 44px;
        letter-spacing: 2px;
        font-size: @commonFontSize;
        flex: 1;
        border-bottom: 1px solid #fff;
        &::-webkit-input-placeholder {
          color: #fff;
        }
      }
      .getCheckNum {
        float: right;
        flex: 1;
        font-size: @commonFontSize;
        text-align: center;
        line-height: 44px;
        font-weight: bolder;
        letter-spacing: 2px;
        color: #fff;
      }
    }
    .protocol {
      width: 100%;
      text-align: center;
      color: #fff;
      font-size: 10px;
      margin-top: 20px;
      em {
        text-decoration: underline;
      }
      input {
        width: 10px;
        height: 10px;
        border: 0;
        outline: none;
      }
    }
    .loginBtn {
      button {
        width: 206px;
        height: 45px;
        border-radius: 6px;
        display: block;
        border: none;
        outline: none;
        color: #ffffff;
        font-size: @commonFontSize;
        margin: 0 auto;
        margin-top: 55px;
        letter-spacing: 5px;
        background: linear-gradient(to right, #c8286e, #a82372);
      }
    }
  }
  .registerBtn {
    button {
      width: 206px;
      height: 45px;
      border-radius: 6px;
      display: block;
      border: none;
      outline: none;
      color: #ffffff;
      font-size: @commonFontSize;
      margin: 0 auto;
      margin-top: 15px;
      letter-spacing: 5px;
      background: RGBA(130, 127, 127, 0.8);
    }
  }
}
</style>




