import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import login from '@/components/login/login'
import register from '@/components/login/register'
import bindingTel from '@/components/login/bindingTel'
import uploadImg from '@/components/login/uploadImg'
// import knowledge from '@/components/knowledge/knowledge'
// import shop from '@/components/shop/shop'
// import search from '@/components/search/search'
// import doctor_detail from '@/components/doctors/doctor_detail'
import mkCircle from '@/components/mkCircle/mkCircle'
import getMoney from '@/components/mkCircle/getMoney'
import award_detail from '@/components/mkCircle/award_detail'
import award_area from '@/components/mkCircle/award_area'
import my_team from '@/components/mkCircle/my_team'
// import store from '@/components/store/store'
// import centerBtn from '@/components/centerBtn/centerBtn'
import hospital_list from '@/components/hospital/hospital_list'
import hospital_detail from '@/components/hospital/hospital_detail'
import booking_search from '@/components/booking/booking_search'
import getAllowance from '@/components/allowance/getAllowance'
Vue.use(Router)

export default new Router({
  routes: [
    //首页
    {
      path: '/',
      name: 'index',
      component: index
    },
    // 登录页面
    {
      path: '/login',
      name: 'login',
      component: login
    },
    // 注册页面
    {
      path: '/register',
      name: 'register',
      component: register
    },
    // 绑定手机号页面
    {
      path: '/bindingTel',
      name: 'bindingTel',
      component: bindingTel
    },
    // 上传头像页面
    {
      path: '/uploadImg',
      name: 'uploadImg',
      component: uploadImg
    },

    // 美咖圈
    {
      path: '/mkCircle',
      name: 'mkCircle',
      component: mkCircle
    },
    // 提现
    {
      path: '/getMoney',
      name: 'getMoney',
      component: getMoney
    },
    // 奖励流水
    {
      path: '/award_detail',
      name: 'award_detail',
      component: award_detail
    },
    // 地区奖励
    {
      path: '/award_area',
      name: 'award_area',
      component: award_area
    },
    // 我的团队
    {
      path: '/my_team',
      name: 'my_team',
      component: my_team
    },

    // 医院列表
    {
      path: '/hospital_list',
      name: 'hospital_list',
      component: hospital_list
    },
    // 医院详情
    {
      path: '/hospital_detail',
      name: 'hospital_detail',
      component: hospital_detail
    },
    {
      path: '/hospital_detail:id',
      name: 'hospital_detail',
      component: hospital_detail
    },
    // 预约查询
    {
      path: '/booking_search',
      name: 'booking_search',
      component: booking_search
    },
    // 领取补贴
    {
      path: '/getAllowance',
      name: 'getAllowance',
      component: getAllowance
    },
    {
      path: '/getAllowance:id',
      name: 'getAllowance',
      component: getAllowance
    },
  ]
})



