<template>
  <div class="container">
    <!-- 头部 -->
    <div class="headers">
      提现
    </div>
    <!-- 提现金额 -->
    <div class="money">
      <h3>提现金额</h3>
      <div class="moneyInp">
        <input type="number" name="" id="" placeholder="请输入您的提现金额" v-model="money">
      </div>
      <div class="ablility">
        <span>您当前可提现金额为：
          <em v-text="allMoney"></em> 元</span>
        <i @click="getAll()">全部提现</i>
      </div>
    </div>
    <!-- 银行卡选择 -->
    <div class="bankcard">
      提现至：
      <Select v-model="bankcard" style="width:80%" placeholder="请选择银行卡" @on-change="selectBankCard">
        <Option v-for="item in bankcardList" :value="item.value" :key="item.value">{{ item.label }} ({{item.bank_name}})</Option>
      </Select>
    </div>
    <!-- 信息填写 -->
    <div class="form">
      <div class="checkNumber">
        <h2>短信验证</h2>
        <input type="number" placeholder="请输入短信验证码" v-model="userCheckNum">
        <button class="getCheck" @click="getCheckNum()" v-text=checkText :disabled="ableGet"></button>
      </div>
      <div class="pwd">
        <h3>交易密码</h3>
        <input type="password" placeholder="请输入交易密码" v-model="userPwd">
      </div>
      <div class="protocol">
        <input type="checkbox" name="" id="isAgree" v-model="isAgree">
        <label for="isAgree">您确认加入则同意
          <a href="javascript:;">《美美咖用户协议》</a>
        </label>
      </div>
      <button class="btn" :disabled=!isAgree @click="getMoney()">确&nbsp;&nbsp;定</button>
    </div>
  </div>
</template>

<script>
import { Toast } from "mint-ui";

export default {
  name: "getMoney",
  data() {
    return {
      // 银行卡列表
      bankcardList: [],
      bankcard: "",
      // 交易密码
      userPwd: "",
      // 验证码
      userCheckNum: "",
      // 提现金额
      money: "",
      // 同意协议
      isAgree: false,
      // 全部金额
      allMoney: "0",
      // 验证码
      checkNum: "",
      checkText: "获取验证码",
      ableGet: false
    };
  },
  methods: {
    onValuesChange(picker, values) {
      if (values[0] > values[1]) {
        picker.setSlotValue(1, values[0]);
      }
    },
    selectBankCard(value) {
      this.bankcard = value;
      console.log(this.bankcard);
    },
    // 获取银行卡
    getBankCard() {
      this.axios
        .post("/api/v2_0/user_cards/list", { page: 1, pageSize: 100 })
        .then(response => {
          let res = response.data;
          console.log(res);
          res.data.data.forEach(element => {
            element.value = element.id;
            element.label = element.card_num
              .replace(/[ \f\t\v]/g, "")
              .replace(/(\d{4})(?=\d)/g, "$1 ");
          });
          this.bankcardList = res.data.data;
          if (this.bankcardList.length == 0) {
            this.toBinding();
          }
        });
    },
    // 去绑定银行卡
    toBinding() {
      alert("请绑定银行卡");
    },
    // 获取验证码
    getCheckNum() {
      this.axios.post("/api/auth/send/msg").then(response => {
        let res = response.data;
      });
    },
    // 全部提现
    getAll() {
      this.money = this.allMoney;
    },
    // 获取验证码
    getCheckNum() {
      let interval;
      this.ableGet = true;
      clearInterval(interval);
      let that = this;
      let time = 90;
      this.axios.post("/api/v2_0/user/sendmsg").then(response => {
        let res = response.data;
        if (res.status_code == 0) {
          interval = setInterval(function() {
            --time;
            that.checkText = time + "秒后可重新发送";
            if (time <= 0) {
              time = 0;
              that.ableGet = false;
              that.checkText = "获取验证码";
            }
          }, 1000);
          Toast({
            message: "发送成功",
            position: "bottom",
            duration: 3000
          });
        } else {
          Toast({
            message: "发送失败，请稍后重试",
            position: "bottom",
            duration: 3000
          });
          this.ableGet = false;
        }
      });
    },
    // 提现（提交）
    getMoney() {
      if (this.money == "") {
        Toast({
          message: "请输入提现金额",
          position: "bottom",
          duration: 3000
        });
        return false;
      } else if (this.money > this.allMoney) {
        Toast({
          message: "可现金额不足",
          position: "bottom",
          duration: 3000
        });
        return false;
      } else if (this.bankcard == "") {
        Toast({
          message: "请选择银行卡",
          position: "bottom",
          duration: 3000
        });
        return false;
      } else if (this.userPwd == "") {
        Toast({
          message: "请输入交易密码",
          position: "bottom",
          duration: 3000
        });
        return false;
      } else if (this.userCheckNum == "") {
        Toast({
          message: "请输入验证码",
          position: "bottom",
          duration: 3000
        });
        return false;
      } else {
        var params = new URLSearchParams();
        params.append("amount", this.money); //提现金额
        params.append("verify_code", this.userCheckNum); //验证码
        params.append("verify_password", this.userPwd); //交易密码
        params.append("card_id", this.bankcard); //银行卡
        this.axios
          .post("/api/v2_0/user_reward/withdraw", params)
          .then(response => {
            let res = response.data;
            console.log(res);
            Toast({
              message: res.message,
              position: "bottom",
              duration: 3000
            });
          });
      }
    }
  },
  created() {
    this.getBankCard();
    this.axios.post("/api/v2_0/user_reward/available_money").then(response => {
      let res = response.data;
      console.log(res);
      this.allMoney = res.data;
    });
  }
};
</script>

<style lang="less" scoped>
.container {
  background-color: #f0f0f0;
  .headers {
    width: 100%;
    height: 44px;
    line-height: 44px;
    font-size: 17px;
    text-align: center;
    color: #444444;
    border-bottom: 1px solid #ccc;
    position: fixed;
    background-color: #fff;
    top: 0;
    z-index: 9999;
  }

  .money {
    margin-top: 44px;
    padding-left: 15px;
    padding-right: 15px;
    overflow: hidden;
    background-color: #fff;
    h3 {
      font-size: 17px;
      color: #666;
      margin: 15px 0;
      padding-left: 10px;
      position: relative;
      &::before {
        content: "";
        display: block;
        height: 14px;
        width: 3px;
        background-color: #35b0ff;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    .moneyInp {
      input {
        width: 100%;
        height: 56px;
        text-align: center;
        font-size: 36px;
        color: #444;
        outline: none;
        border: 0;
        padding: 0;
        border-bottom: 1px solid #d8d8d8;
        &::placeholder {
          color: #ccc;
        }
      }
    }
    .ablility {
      margin: 15px 0;
      font-size: 14px;
      color: #444;
      em {
        color: #6591ff;
      }
      i {
        float: right;
        color: #6591ff;
        font-style: normal;
      }
    }
  }

  .bankcard {
    width: 100%;
    height: 44px;
    line-height: 44px;
    margin-top: 15px;
    background-color: #fff;
    overflow: hidden;
    font-size: 14px;
    padding: 0 15px;
    .ivu-select,
    .ivu-select-single {
      display: block;
      margin: 0px auto;
      height: 100%;
      float: right;
      margin-right: 10px;
      margin-top: 6px;
    }
    .ivu-select-single .ivu-select-selection {
      height: 44px;
    }
  }

  .form {
    padding: 15px;
    margin-top: 10px;
    background-color: #fff;
    h3,
    h2 {
      font-size: 17px;
      color: #666;
      margin: 15px 0;
      padding-left: 10px;
      position: relative;
      &::before {
        content: "";
        display: block;
        height: 14px;
        width: 3px;
        background-color: #35b0ff;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    h3 {
      &::before {
        background-color: #d93aff;
      }
    }
    .checkNumber {
      overflow: hidden;
      input {
        height: 44px;
        line-height: 44px;
        font-size: 17px;
        width: 50%;
        border: none;
        border-bottom: 1px solid #d8d8d8;
        outline: none;
      }
      .getCheck {
        float: right;
        width: 50%;
        text-align: center;
        line-height: 44px;
        color: #444;
        border: none;
        outline: none;
        padding: 0;
        font-size: 17px;
        font-weight: bold;
      }
    }
    .pwd {
      overflow: hidden;
      input {
        height: 44px;
        line-height: 44px;
        font-size: 17px;
        width: 100%;
        border: none;
        border-bottom: 1px solid #d8d8d8;
        outline: none;
      }
    }
    input::placeholder {
      color: #ccc;
    }
    .protocol {
      width: 100%;
      text-align: center;
      margin-top: 30px;
      color: #444;
      font-size: 14px;
      input {
        width: 16px;
        height: 16px;
        background-color: #d8d8d8;
        border: none;
        outline: none;
        vertical-align: middle;
      }
      a {
        color: #796fff;
        text-decoration: underline;
      }
    }
    .btn {
      width: 206px;
      height: 45px;
      display: block;
      margin: 0 auto;
      margin-top: 7rem;
      background: linear-gradient(to right, #c8286e, #a82372);
      border: none;
      outline: none;
      border-radius: 5px;
      color: #fff;
      padding: 0;
    }
  }
}
</style>





