<template>
    <div class="container">
        <header>我的团队</header>
        <div class="mui-content">
            <div>
                <div id="segmentedControl" class="mui-segmented-control">
                    <div class="mui-control-item" :class=" {'mui-active':isActive}" @click="toggle1()">
                        代理商
                    </div>
                    <div class="mui-control-item" :class=" {'mui-active':!isActive}" @click="toggle2()">
                        合伙人
                    </div>
                </div>
            </div>
            <div>
                <div id="item1" class="mui-control-content mui-active" v-if="isActive">
                    <div id="scroll" class="mui-scroll-wrapper">
                        <div class="mui-scroll">
                            <ul class="mui-table-view">
                                <li class="mui-table-view-cell">
                                    <span>姓名</span>
                                    <span>手机</span>
                                    <span>级别</span>
                                    <span>当月业绩</span>
                                </li>
                                <li class="mui-table-view-cell" v-for="item in list1" :key="item.id">
                                    <strong>{{item.realname}}</strong>
                                    <em>
                                        <a :href=item.href>{{item.username}}</a>
                                    </em>
                                    <em>{{item.role_name}}</em>
                                    <i>{{item.total}}</i>
                                </li>

                            </ul>
                        </div>
                    </div>
                </div>
                <div id="item2" class="mui-control-content" v-if="!isActive">
                    <ul class="mui-table-view">
                        <li class="mui-table-view-cell">
                            <span>姓名</span>
                            <span>手机</span>
                            <span>级别</span>
                            <span>当月业绩</span>
                        </li>
                        <li class="mui-table-view-cell" v-for="value in list2" :key="value.id">
                            <strong>{{value.realname}}</strong>
                            <em>
                                <a :href=value.href>{{value.username}}</a>
                            </em>
                            <em>{{value.role_name}}</em>
                            <i>{{value.total}}</i>
                        </li>

                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: "my_team",
  data() {
    return {
      isActive: true,
      //   代理商列表
      list1: [],
      //   合伙人列表
      list2: []
    };
  },
  methods: {
    toggle1() {
      this.isActive = true;
    },
    toggle2() {
      this.isActive = false;
    },
    getLists() {
      // 获取代理商列表
      this.axios
        .post("/api/v2_0/user_reward/team_list", { role: 1 })
        .then(response => {
          let res = response.data;
          console.log(res);
          this.list1 = res.data;
          this.list1.forEach(element => {
            element.href = "tel:" + element.username;
          });
        });
      // 获取合伙人列表
      this.axios
        .post("/api/v2_0/user_reward/team_list", { role: 2 })
        .then(response => {
          let res = response.data;
          console.log(res);
          this.list2 = res.data;
        });
    }
  },
  created() {
    this.getLists();
  }
};
</script>

<style lang="less" scoped>
.container {
  header {
    width: 100%;
    height: 44px;
    line-height: 44px;
    font-size: 17px;
    text-align: center;
    color: #444444;
    border-bottom: 1px solid #ccc;
    position: fixed;
    background-color: #fff;
    top: 0;
    z-index: 999;
  }

  .mui-card .mui-control-content {
    padding: 10px;
  }

  .mui-control-content {
    height: 150px;
  }

  .mui-scroll-wrapper {
    overflow: visible !important;
    overflow: auto;
  }

  .mui-content {
    margin-top: 44px;
    .mui-segmented-control .mui-control-item.mui-active {
      background-color: transparent;
      border: none;
    }
    .mui-segmented-control {
      border: 0;
    }
    .mui-segmented-control .mui-control-item {
      width: 0;
      border-left: 1px solid transparent !important;
      color: #888888;
    }
    .mui-segmented-control .mui-control-item.mui-active {
      color: #ce296e;
      position: relative;
      &::before {
        content: "";
        width: 42px;
        height: 2px;
        background: rgba(206, 41, 110, 1);
        display: block;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
      }
    }
    .mui-segmented-control {
      background-color: #fff;
    }
    .mui-table-view-cell:nth-of-type(odd) {
      background-color: #ccc;
    }
    .mui-segmented-control {
      padding: 0 120px;
    }
  }

  .mui-table-view-cell {
    width: 100%;
    display: flex;
    span,
    strong,
    em,
    a,
    i {
      flex: 1;
      text-align: center;
      font-size: 14px;
    }
    span {
      color: #666;
    }
    strong {
      color: #222;
      font-weight: bold;
    }
    em,
    a {
      color: #888888;
    }
    i {
      color: #444444;
      font-style: normal;
      font-weight: bold;
    }
  }
}
</style>





