<template>
  <div class="container">
    <!-- 头部 -->
    <div class="header">
      <mt-header title="领取补贴">
        <router-link to="/" slot="left">
          <mt-button icon="back"></mt-button>
        </router-link>
      </mt-header>
    </div>
    <div class="orderIndex">
      订单号：
      <em v-text="orderNum"></em>
    </div>
    <!-- 消费金额 -->
    <div class="money">
      <h3>消费金额</h3>
      <div class="moneyInp">
        <input type="number" name="" id="" placeholder="请输入您的消费金额" v-model="money">
      </div>
    </div>
    <div class="upload">
      <h3>上传凭证</h3>
      <ul class="upload-controller">
        <!-- 上传消费凭证 -->
        <li>
          <el-upload class="avatar-uploader" name="image" action="http://test.api2.ameimeika.com/api/v2_0/upload/upload_img" :headers=headers :show-file-list="false" :on-success="handleAvatarSuccess1" :before-upload="beforeAvatarUpload">
            <img v-if="consumeImg" :src="consumeImg" class="avatar">
            <div class="tip" v-else>
              <Icon type="ios-camera"></Icon>
              <h4>请上传消费凭证</h4>
              <p>（POS单、消费单、流水记录、转账记录)</p>
            </div>
          </el-upload>
        </li>
        <!-- 上传手术照片 -->
        <li>
          <el-upload class="avatar-uploader" name="image" action="http://test.api2.ameimeika.com/api/v2_0/upload/upload_img" :headers=headers :show-file-list="false" :on-success="handleAvatarSuccess2" :before-upload="beforeAvatarUpload">
            <img v-if="operationImg" :src="operationImg" class="avatar">
            <div class="tip" v-else>
              <Icon type="ios-camera"></Icon>
              <h4>请上传手术照片</h4>
              <p>（手术之后的照片）</p>
              <p>&nbsp;</p>
            </div>
          </el-upload>
        </li>
      </ul>
    </div>
    <!-- 信息填写 -->
    <div class="form">
      <div class="checkNumber">
        <h2>短信验证</h2>
        <input type="number" placeholder="请输入您的短信验证码" v-model="checkNum">
        <button class="getCheck" @click="getCheckNum()" v-text=checkText :disabled="ableGet"></button>
      </div>
      <div class="pwd">
        <h3>交易密码</h3>
        <input type="password" placeholder="请输入您的交易密码" v-model="userPwd">
      </div>
      <div class="protocol">
        <input type="checkbox" name="" id="isAgree" v-model="isAgree">
        <label for="isAgree">您确认加入则同意
          <a href="javascript:;">《美美咖用户协议》</a>
        </label>
      </div>
      <button class="btn" :disabled=!isAgree @click="getAllowance()">确&nbsp;&nbsp;定</button>

    </div>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
export default {
  name: "getAllowance",
  data() {
    return {
      // 消费凭证
      consumeImg: "",
      // 手术照片
      operationImg: "",
      headers: { Authorization: "Bearer " + localStorage.token },
      // 订单号
      orderNum: "158987585658985214",
      // 是否同意协议
      isAgree: false,
      // 消费金额
      money: "",
      // 验证码
      checkNum: "",
      checkText: "获取验证码",
      ableGet: false,
      // 用户交易密码
      userPwd: ""
    };
  },
  methods: {
    onValuesChange(picker, values) {
      if (values[0] > values[1]) {
        picker.setSlotValue(1, values[0]);
      }
    },
    // 选择银行卡
    selectBankCard(value) {
      console.log(value);
    },
    //消费凭证上传控件
    handleAvatarSuccess1(res, file) {
      this.consumeImg = this.imgUrl + res.data.url;
      console.log(this.consumeImg);
    },
    //手术照片上传控件
    handleAvatarSuccess2(res, file) {
      this.operationImg = this.imgUrl + res.data.url;
      console.log(this.operationImg);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;
      // if (!isJPG) {
      //   alert("上传图片只能是 JPG 格式!");
      // }
      if (!isLt2M) {
        alert("上传图片大小不能超过 2MB!");
      }
    },
    // 获取验证码
    getCheckNum() {
      let interval;
      this.ableGet = true;
      clearInterval(interval);
      let that = this;
      let time = 90;
      this.axios.post("/api/v2_0/user/sendmsg").then(response => {
        let res = response.data;
        if (res.status_code == 0) {
          interval = setInterval(function() {
            --time;
            that.checkText = time + "秒后可重新发送";
            if (time <= 0) {
              time = 0;
              that.ableGet = false;
              that.checkText = "获取验证码";
            }
          }, 1000);
          Toast({
            message: "发送成功",
            position: "bottom",
            duration: 3000
          });
        } else {
          Toast({
            message: "发送失败，请稍后重试",
            position: "bottom",
            duration: 3000
          });
          this.ableGet = false;
        }
      });
    },
    //提交
    getAllowance() {
      if (this.money.trim() == "") {
        Toast({
          message: "请输入消费金额",
          position: "bottom",
          duration: 3000
        });
        return false;
      } else if (this.checkNum.trim() == "") {
        Toast({
          message: "请输入验证码",
          position: "bottom",
          duration: 3000
        });
        return false;
      } else if (this.userPwd.trim() == "") {
        Toast({
          message: "请输入交易密码",
          position: "bottom",
          duration: 3000
        });
        return false;
      } else if (this.consumeImg.trim() == "") {
        Toast({
          message: "请上传消费凭证",
          position: "bottom",
          duration: 3000
        });
        return false;
      } else if (this.operationImg.trim() == "") {
        Toast({
          message: "请上传手术照片",
          position: "bottom",
          duration: 3000
        });
        return false;
      } else {
        alert('提交后您的信息将无法修改，确认提交吗？')
        var params = new URLSearchParams();
        params.append("id", this.orderNum);
        params.append("money", this.money);
        params.append("img1", this.consumeImg);
        params.append("img2", this.operationImg);
        params.append("verfity_code", this.checkNum);
        params.append("verfity_password", this.userPwd);
        this.axios
          .post(
            "/api/v2_0/beauty_project_order/allowance_apply",
            params
            // {
            //     id: this.orderNum, //订单号
            //     money: this.money, //补偿金额
            //     img1: this.consumeImg, //消费凭证
            //     img2: this.operationImg, //手术照片
            //     verfity_code: this.checkNum, //验证码
            //     verfity_password: this.userPwd //交易密码
            // }
          )
          .then(response => {
            let res = response.data;
            console.log(res);
          });
      }
    }
  },
  mounted() {
    let id = location.hash.split(":")[1];
    this.orderIndex = id;
  }
};
</script>

<style lang="less" scoped>
.container {
  background-color: #f0f0f0;
  top: 0;
  padding-top: 64px;
  //   头部
  .header {
    background: #fff;
    height: 64px;
    z-index: 99999;
    padding-top: 20px;
    position: fixed;
    top: 0;
    width: 100%;
    .mint-header {
      background-color: #fff;
      color: #444444;
      min-height: 44px;
      line-height: 44px;
      font-size: 17px;
    }
  }
  .orderIndex {
    height: 44px;
    line-height: 44px;
    font-size: 14px;
    padding: 0 15px;
    color: #999;
  }
  //提现金额
  .money {
    padding-left: 15px;
    padding-right: 15px;
    overflow: hidden;
    background-color: #fff;
    h3 {
      font-size: 17px;
      color: #666;
      margin: 15px 0;
      padding-left: 10px;
      position: relative;
      &::before {
        content: "";
        display: block;
        height: 14px;
        width: 3px;
        background-color: #ff6b6b;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }

    .moneyInp {
      input {
        width: 100%;
        height: 56px;
        padding: 0;
        text-align: center;
        font-size: 36px;
        color: #cccccc;
        outline: none;
        border: 0;
        border-bottom: 1px solid #d8d8d8;
        &::placeholder {
          color: #ccc;
        }
      }
    }
  }
  //上传凭证
  .upload {
    padding-left: 15px;
    padding-right: 15px;
    overflow: hidden;
    background-color: #fff;
    h3 {
      font-size: 17px;
      color: #666;
      margin: 15px 0;
      padding-left: 10px;
      position: relative;
      &::before {
        content: "";
        display: block;
        height: 14px;
        width: 3px;
        background-color: #6771ff;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    .upload-controller {
      width: 100%;
      height: 165px;
      margin-bottom: 15px;
      display: flex;
      justify-content: space-between;
      li {
        height: 100%;
        width: 165px;
        border: 1px solid #cbcbcb;
        border-radius: 10px;
        position: relative;
        .avatar-uploader .el-upload {
          border: 1px dashed #d9d9d9;
          border-radius: 6px;
          cursor: pointer;
          position: relative;
          overflow: hidden;
        }
        .avatar-uploader .el-upload:hover {
          border-color: #409eff;
        }
        .tip {
          width: 100%;
          color: #8c939d;
          text-align: center;
          position: absolute;
          top: 50%;
          transform: translatey(-50%);
          i {
            font-size: 40px;
            color: #cbcbcb;
          }
          h4 {
            font-size: 14px;
            color: #666666;
          }
          p {
            width: 100%;
            font-size: 8px;
            color: #999;
          }
        }
        .avatar {
          width: 165px;
          height: 165px;
          display: block;
          margin: 0 auto;
        }
      }
    }
  }

  //填写表单

  .form {
    padding: 15px;
    margin-top: 10px;
    background-color: #fff;
    h3,
    h2 {
      font-size: 17px;
      color: #666;
      margin: 15px 0;
      padding-left: 10px;
      position: relative;
      &::before {
        content: "";
        display: block;
        height: 14px;
        width: 3px;
        background-color: #35b0ff;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    h3 {
      &::before {
        background-color: #d93aff;
      }
    }
    .checkNumber {
      overflow: hidden;
      input {
        height: 44px;
        line-height: 44px;
        font-size: 17px;
        width: 60%;
        border: none;
        border-bottom: 1px solid #d8d8d8;
        outline: none;
      }
      .getCheck {
        float: right;
        width: 40%;
        text-align: center;
        line-height: 44px;
        font-weight: 700;
        color: #444;
        padding: 0;
        border: 0;
        outline: none;
      }
    }
    .pwd {
      overflow: hidden;
      input {
        height: 44px;
        line-height: 44px;
        font-size: 17px;
        width: 100%;
        border: none;
        border-bottom: 1px solid #d8d8d8;
        outline: none;
      }
    }
    input::placeholder {
      color: #ccc;
    }
    .protocol {
      width: 100%;
      text-align: center;
      margin-top: 30px;
      color: #444;
      font-size: 14px;
      input {
        width: 16px;
        height: 16px;
        background-color: #d8d8d8;
        border: none;
        outline: none;
        vertical-align: middle;
      }
      a {
        color: #796fff;
        text-decoration: underline;
      }
    }
    .btn {
      width: 206px;
      height: 45px;
      line-height: 45px;
      text-align: center;
      padding: 0;
      display: block;
      margin: 0 auto;
      margin-top: 44px;
      margin-bottom: 30px;
      background: linear-gradient(to right, #c8286e, #a82372);
      border: none;
      outline: none;
      border-radius: 5px;
      color: #fff;
    }
  }
}
</style>





