<template>
  <div class="container">
    <div class="title">
      <div class="logo">
        <img src="../../assets/images/icon_register_logo@3x.png" alt="">
      </div>
      <div class="text">
        登录注册
      </div>
    </div>
    <div class="loginForm">
      <div class="tel">
        <input type="tel" placeholder="请输入手机号" class="userTel" v-model="userTel">
        <span><img src="../../assets/images/icon_first_phone@3x.png" alt=""></span>
      </div>
      <div class="pwd">
        <input type="password" placeholder="请输入登录密码" class="userPwd" v-model="userPwd">
        <span><img src="../../assets/images/icon_first_password@3x.png" alt=""></span>
        <router-link to="/forgetPwd" class="forgetPwd">忘记密码 ?</router-link>
      </div>
      <div class="loginBtn">
        <button @click="login()">
          进入美美咖
        </button>
      </div>
    </div>
    <div class="registerBtn">
      <router-link to="/register">
        我要注册
      </router-link>
    </div>

  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      userTel: "",
      userPwd: ""
    };
  },
  methods: {
    login() {
      this.axios
        .post("/api/auth/login", {
          username: this.userTel,
          password: this.userPwd,
          mode: 2
        })
        .then(response => {
          let res = response.data;
          localStorage.setItem("token", res.data.token);
          console.log(localStorage.token);
        });
    }
  }
};
</script>

<style lang="less" scoped>
@commonFontSize: 15px;
.container {
  height: 100%;
  background: #666;
  overflow: hidden;
  padding: 0 15px;

  .title {
    height: 40px;
    margin-top: 80px;
    .logo {
      height: 100%;
      float: left;
      margin-right: 10px;
      img {
        height: 100%;
      }
    }
    .text {
      line-height: 40px;
      color: #fff;
      font-size: 2rem;
    }
  }
  .loginForm {
    padding-top: 35px;
    overflow: hidden;
    .userTel,
    .userPwd {
      height: 44px;
      width: 100%;
      margin-bottom: 15px;
      border: none;
      background-color: transparent;
      outline: none;
      padding-left: 28px;
      color: #fff;
      line-height: 44px;
      letter-spacing: 2px;
      font-size: @commonFontSize;
      border-bottom: 1px solid #fff;
      &::-webkit-input-placeholder {
        color: #fff;
      }
      &::-moz-placeholder {
        /* Mozilla Firefox 19+ */
        color: #fff;
      }
      &:-moz-placeholder {
        /* Mozilla Firefox 4 to 18 */
        color: #fff;
      }
      &:-ms-input-placeholder {
        /* Internet Explorer 10-11 */
        color: #fff;
      }
    }
    .tel,
    .pwd {
      position: relative;
      span {
        width: 13px;
        height: 18px;
        display: block;
        position: absolute;
        top: 10px;
        left: 0;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
    .forgetPwd {
      float: right;
      font-size: 14px;
      color: rgba(255, 255, 255, 1);
      font-weight: bolder;
      margin-top: 10px;
    }
    .loginBtn {
      button {
        width: 206px;
        height: 45px;
        border-radius: 6px;
        display: block;
        border: none;
        outline: none;
        color: #ffffff;
        font-size: @commonFontSize;
        margin: 0 auto;
        margin-top: 55px;
        letter-spacing: 5px;
        background: linear-gradient(to right, #c8286e, #a82372);
      }
    }
  }
  .registerBtn {
    a {
      width: 206px;
      height: 45px;
      line-height: 45px;
      text-align: center;
      border-radius: 6px;
      display: block;
      border: none;
      outline: none;
      color: #ffffff;
      font-size: @commonFontSize;
      margin: 0 auto;
      margin-top: 15px;
      letter-spacing: 5px;
      background: RGBA(130, 127, 127, 0.8);
    }
  }
}
</style>





