// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import '@/assets/css/base.css'
//element ui
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);
//iview
import iView from 'iview';
import 'iview/dist/styles/iview.css';
Vue.use(iView);
//mint ui
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
Vue.use(MintUI);
//vue-axios
import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios);
Vue.config.productionTip = false
axios.defaults.baseURL = 'http://test.api2.ameimeika.com';
axios.defaults.headers.common['Authorization'] = "Bearer " + (localStorage.token || '');
//baseurl
import imgUrl from '@/assets/js/baseUrl'
Vue.prototype.imgUrl = imgUrl.imgUrl
//get Data
import './assets/js/getData'
// 引入echarts
import echarts from 'echarts'
Vue.prototype.$echarts = echarts
//vue滑块
import VueAwesomeSwiper from 'vue-awesome-swiper'
// require styles
import 'swiper/dist/css/swiper.css'
Vue.use(VueAwesomeSwiper)
//muse ui
import MuseUI from 'muse-ui'
import 'muse-ui/dist/muse-ui.css'
Vue.use(MuseUI)
//mui
import "@/assets/lib/mui-master/dist/css/mui.css"

// import '@/assets/js/getData.js'
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



