<template>
    <div class="container">
        <header>
            奖励流水
        </header>
        <ul class="list">
            <li v-for="item in detail_list" :key="item.id">
                <div class="left">
                    <div class="awardClass">
                        用户消费奖励
                    </div>
                    <div class="awardTime">
                        {{item.time}}
                    </div>
                </div>
                <div class="right">
                    <strong>{{item.trans_type}}</strong>
                    {{item.amount}}
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
  name: "award_detail",
  data() {
    return {
      detail_list: []
    };
  },
  methods: {
    getData() {
      this.axios
        .post("/api/v2_0/user_reward/reward_trade", {
          page: 1,
          pageSize: 1000
        })
        .then(response => {
          let res = response.data;
          this.detail_list = res.data.data;
          this.detail_list.forEach(element => {
            element.time = new Date(
              Math.floor(element.time + "000")
            ).toLocaleString();
            element.trans_type = element.trans_type == "1" ? "+" : "-";
          });
          console.log(this.detail_list);
        });
    }
  },
  created() {
    this.getData();
  }
};
</script>

<style lang="less" scoped>
.container {
  header {
    width: 100%;
    height: 44px;
    line-height: 44px;
    font-size: 17px;
    text-align: center;
    color: #444444;
    border-bottom: 1px solid #ccc;
    position: fixed;
    background-color: #fff;
    top: 0;
    z-index: 999;
  }

  .list {
    width: 100%;
    margin-top: 44px;
    overflow: auto;
    padding: 0 15px;
    box-sizing: border-box;
    li {
      height: 66px;
      border-bottom: 1px solid #e4e4e4;
      overflow: hidden;
      color: #444444;
      .left,
      .right {
        height: 100%;
      }
      .left {
        width: 50%;
        float: left;
        position: relative;
        .awardClass {
          position: absolute;
          top: 10px;
          font-size: 17px;
        }
        .awardTime {
          position: absolute;
          bottom: 10px;
          font-size: 12px;
          color: #888;
        }
      }
      .right {
        float: right;
        line-height: 66px;
        font-size: 25px;
      }
    }
  }
}
</style>





